$main-background-color: #2b2d33;
$grey-color: #9a9b9e;
$light-grey-color: #dedfdf;
$light-dark-color: #25272b;
$main-font-color: #ffffff;
$main-accent-color: #71b872;
$green-middle-color: #0a7d23;
$dark-green: #233828;
$dark-color: #17191e;
$blue-color: #6fabff;
$button-background: linear-gradient(45deg, #4294ff 0%, #42c8d1 100%);
$button-background-hover: linear-gradient(45deg, #59a1ff 0%, #33f1fe 100%);

@font-face {
  font-family: "ubuntu";
  src: url("../fonts/Ubuntu/Ubuntu-Bold.ttf") format("truetype"),
    url("../fonts/Ubuntu/Ubuntu-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "ubuntu";
  src: url("../fonts/Ubuntu/Ubuntu-Regular.ttf") format("truetype"),
    url("../fonts/Ubuntu/Ubuntu-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "ubuntu";
  src: url("../fonts/Ubuntu/Ubuntu-Light.ttf") format("truetype"),
    url("../fonts/Ubuntu/Ubuntu-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

body {
  font-size: 17px;
  line-height: 180%;
  background-color: $main-background-color;
  color: $main-font-color;
  font-family: "ubuntu", sans-serif;
  font-weight: 300;
}

html,
body {
  // overflow-x: hidden;
}
a._color-blue:hover {
  text-decoration: underline;
}
img {
  user-select: none;
  pointer-events: none;
}

// common

._color-blue {
  color: $blue-color;
}
//

.page {
  overflow: hidden;
}
.wrap {
  display: flex;
  flex-wrap: wrap;
  max-width: 1440px;
  margin: 0 auto;
  padding: 116px 80px;
}

.container {
  width: 100%;
}
.title {
  position: relative;
  font-weight: 700;
  font-size: 52px;
  line-height: 138%;
}
.title::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -16px;
  height: 6px;
  display: block;
  width: 220px;
  border-radius: 10px;
  background-color: $main-accent-color;
}

.title-mobile {
  display: none;
  margin-bottom: 32px;
  font-weight: 700;
  font-size: 42px;
  line-height: 138%;
  text-align: center;
}

.button {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 420px;
  padding: 17px 0;
  background: $button-background;
  border-radius: 10px;
  font-size: 22px;
  line-height: 164%;
  // filter: drop-shadow(0px 36px 14px rgba(0, 0, 0, 0.01))
  //   drop-shadow(0px 20px 12px rgba(0, 0, 0, 0.03))
  //   drop-shadow(0px 9px 9px rgba(0, 0, 0, 0.06))
  //   drop-shadow(0px 2px 5px rgba(0, 0, 0, 0.06))
  //   drop-shadow(0px 0px 0px rgba(0, 0, 0, 0.07));
  transition: background-color 200ms ease, transform 200ms ease;

  &__text {
    position: relative;
    margin-left: 50px;
  }
  &__text:before {
    content: "";
    position: absolute;
    top: 4px;
    left: -50px;
    width: 30px;
    height: 26px;
    background-image: url("../img/common/airplane.svg");
  }
}

.button * {
  z-index: 1;
}
.button:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  z-index: -10;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background: $button-background-hover;
  opacity: 0;
}
.button:hover:before {
  opacity: 1;
}
.button:hover {
  transform: scale(1.05);
}

._align-start {
  align-self: start;
}
._align-end {
  align-self: end;
}

@media (max-width: 1200px) {
  .wrap {
    padding: 56px 40px;
  }
}
@media (max-width: 960px) {
  .wrap {
    padding: 56px 32px;
  }
}
@media (max-width: 770px) {
  .wrap {
    padding: 56px 16px;
  }
  .button {
    gap: 10px;
  }
}
@media (max-width: 420px) {
  body {
    font-size: 17px;
  }
  .button {
    padding: 17px 5px;
    width: 100%;
    font-size: 18px;
    &__text:before {
      top: 0;
    }
  }
}

// json-animations

.json-anim-footer {
  margin: 0 auto -50px;
  max-width: 500px;
}

.json-anim-page-request {
  position: absolute;
  top: auto;
  left: auto;
  right: 0;
  bottom: -175px;
  width: 311px;
  height: 294px;
  transform: rotate(-10deg);
}
.json-anim-page-request {
  position: absolute;
  top: auto;
  left: auto;
  right: 0;
  bottom: -175px;
  width: 311px;
  height: 294px;
  transform: rotate(-10deg);
}
@media (max-width: 1200px) {
  .json-anim-page-request {
    opacity: 0;
  }
}
// header
.page-header {
  &__wrap {
    padding: 34px 32px;
    width: 100%;
    max-width: 1920px;
  }
  &__logo a {
    display: flex;
    align-items: center;
    gap: 16px;
    font-weight: 700;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }

  .header-menu {
    // .header-menu__list

    &__list {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: flex-end;
      gap: 16px;
      letter-spacing: 0.5px;
    }

    // .header-menu__list-item
    &__list-item > a {
      display: block;
      padding: 11px 32px;
      font-size: 17px;
      border-radius: 5px;
      transition: all 0.2s;
    }
    &__list-item > a:hover {
      background-color: $dark-color;
    }
    &__list-item {
      // .header-menu__list-item_outlined

      &_outlined > a {
        border: 2px solid $main-accent-color;
      }
      &_outlined > a:hover {
        background-color: $main-accent-color;
      }
    }
  }
}

@media (max-width: 1200px) {
  .page-header {
    &__wrap {
      padding: 30px 10px;
    }
    .header-menu {
      &__list {
        gap: 10px;
      }
      &__list-item a {
        font-size: 13px;
        padding: 8px 20px;
      }
    }
  }
}
@media (max-width: 960px) {
  .page-header {
    &__wrap {
      padding: 30px 10px;
    }
    .header-menu {
      &__list {
        gap: 6px;
      }
      &__list-item a {
        padding: 8px 10px;
        font-size: 14px;
      }
    }
  }
}
@media (max-width: 770px) {
  .page-header {
    &__wrap {
      padding-top: 16px;
      padding-bottom: 32px;
    }
    &__logo {
      width: 100%;
      display: flex;
      justify-content: center;
    }
    .header-menu {
      display: none;
    }
  }
}
.planet {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 87px;
  height: 87px;
