// presentation
.page-presentation {
  &__container {
    width: 100%;
  }
  &__header {
    display: flex;
    width: 100%;
    margin-bottom: 300px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 380px;
    padding-top: 70px;
  }

  &__title {
    margin-bottom: 24px;
    max-width: 400px; //чтобы слова переносились как в макете
    font-weight: 700;
    font-size: 52px;
    line-height: 68px;
    font-weight: 700;
  }

  &__subtitle {
    margin-bottom: 64px;
    font-size: 22px;
    line-height: 209%;
    word-spacing: 3px;
  }
  &__subtitle-number {
    padding: 4px 14px;
    font-weight: 700;
    border-radius: 10px;
    background: $dark-color;
  }
  &__button {
    flex: 0 0 auto;
    padding: 17px 64px;
  }

  // .page-presentation__view

  &__view {
    display: flex;
    flex-grow: 1;
    justify-content: right;
    padding-right: 13%;
  }

  // .page-presentation__view-item

  &__view-item {
    position: relative;
    width: 316px;
    height: 645px;
    background-image: url("../img/presentation/header/iPhone14.png");
    background-size: 100%;
    background-repeat: no-repeat;

    &::after {
      content: "";
      position: absolute;
      z-index: -2;
      top: 0;
      // из-за кнопок фон прозрачный просвечивается по бокам и нарушает тень, потому смещаем внутрь ее
      right: 7px;
      left: 7px;
      bottom: 0;
      border-radius: 20%;
      box-shadow: 0 100px 50px 20px rgba(29, 30, 34, 0.5);
    }
  }

  // .page-presentation__view-back

  &__view-back {
    position: absolute;
    z-index: -2;
    top: calc(50% - (1102px / 2) - 20px);
    left: calc(50% - (1102px / 2));
    width: 1102px;
    height: 1102px;
    // background-image: url("../img/presentation/header/background.png");
    background-repeat: no-repeat;
  }
  &__orbit {
    position: absolute;
    z-index: -2;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;

    border: 11px solid #25272b39;
    border-radius: 50%;

    &::before {
      content: "";
      position: absolute;
      display: block;
      top: 13%;
      left: 13%;
      height: 74%;
      width: 74%;
      opacity: 1;
      border-radius: 50%;
      border: 11px solid #25272b81;
    }
    &:after {
      content: "";
      position: absolute;
      left: 25%;
      top: 25%;
      height: 50%;
      width: 50%;
      border: 11px solid $light-dark-color;
      border-radius: 50%;
    }
  }

  &__footer {
    display: flex;
    gap: 32px;
  }
  &__tg-only {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  &__tg-benefits {
    padding-top: 54px;
  }
}

.tg-benefits__item {
  display: flex;
  gap: 32px;
  margin-bottom: 48px;
  line-height: 28px;
}
.tg-benefits__item:hover .tg-benefits__icon {
