}
.tg-benefits__text {
  font-size: 18px;
  // font-weight: 400;
}

.tg-only__title {
  margin-top: -92px;
  margin-bottom: 38px;
  max-width: 400px;
  font-size: 32px;
}
.tg-only__title::after {
  left: 22%;
  width: 54%;
}

