// about

.page-about {
  // .page-about__container

  // .page-about__header

  &__header {
    display: flex;
    align-items: stretch;
    gap: 32px;
    margin-bottom: 48px;
  }

  // .page-about__video

  &__video {
    position: relative;
    overflow: hidden;
    width: 66%;
    padding-bottom: 37%;
    border-radius: 10px;
  }
  &__video iframe {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  // .page-about__header-info

  &__header-info {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    padding-top: 12px;
    width: 34%;
  }

  // .page-about__title

  &__title {
    margin-bottom: 54px;
  }

  // .page-about__item

  &__item {
    flex: 1 0 auto;
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 32px;
    border: 6px solid $light-dark-color;
    border-radius: 10px;
    transition: all 0.2s;
  }
  &__item:hover {
    border-color: $dark-color;
  }
  &__item:hover &__item-icon {
    background-color: $dark-color;
  }

  // .page-about__item-icon

  &__item-icon {
    position: absolute;
    right: -22px;
    top: -22px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    border-radius: 10px;
    background-color: $light-dark-color;
    background-repeat: no-repeat;
    background-position: center;
    transition: all 0.2s;
  }

  // .page-about__item-title

  &__item-title {
    font-weight: 700;
  }

  // .page-about__item-note

  &__item-note {
    padding: 16px;
    background: $light-dark-color;
    border-radius: 10px;
  }

  &__bottom {
    display: grid;
    gap: 48px;
    grid-template-rows: auto auto 1fr;
    grid-template-columns: 1fr 1fr 1fr;
  }

  &__team {
    grid-column: 2/4;
    grid-row: 1/3;
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 16px;
    align-content: space-between;
    justify-content: space-between;
    padding: 28px;
    border: 6px solid $light-dark-color;
    border-radius: 10px;
    transition: all 0.2s;
  }
  &__team:hover {
    border-color: $dark-color;
  }
  &__team-info {
    grid-column: 1/3;
  }
  &__team-title {
    margin-bottom: 16px;
    font-weight: 700;
  }

  &__team-member:hover,
  &__team-member_other:hover {
    background: $dark-color;
  }
  &__team-member_other:hover .member__other-mask {
    background: rgb(23, 25, 30);
    background: linear-gradient(
      81deg,
      rgba(23, 25, 30, 0.023809523809523836) 16%,
      rgba(23, 25, 30, 0.6190476190476191) 80%,
      rgba(23, 25, 30, 0.7952380952380953) 90%
    );
  }

  &__team-member_other {
    grid-column: 4/6;
    display: flex;
    justify-content: flex-start;
    padding: 18px 16px 16px;
    border-radius: 10px;
    background: $light-dark-color;
    transition: all 0.2s;
  }
}

.member {
  padding: 18px 16px 16px;
  border-radius: 10px;
  background: $light-dark-color;
  transition: all 0.2s;
  &__photo {
    display: flex;
    align-items: center;
    justify-content: left;
    margin-bottom: 16px;
  }
  &__photo img {
    border-radius: 50%;
  }
  &__position {
    font-size: 13px;
    line-height: 147%;
    color: $grey-color;
  }

  &__other-photo {
    position: relative;
    display: flex;
    justify-content: flex-start;
    margin-bottom: 20px;
    overflow: hidden;
  }
  &__other-mask {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(270deg, #25272b 0%, rgba(37, 39, 43, 0) 100%);
  }
  &__other-photo img {
    border-radius: 50%;
    border: 5px solid $light-dark-color;
  }
  &__other-photo img + img {
    margin-left: -23px;
  }
}

.cost {
