// reviews

.page-reviews {
  // .page-reviews__wrap

  // .page-reviews__container

  &__container {
    column-count: 3;
    column-gap: 32px;
  }

  // .page-reviews__title

  &__title {
    margin-bottom: 102px;
    max-height: 80px;
  }
  &__title::after {
    width: 100%;
    max-width: 211px;
  }
  &__title-mobile {
    display: none;
  }

  // .page-reviews__item

  &__item {
    display: inline-block; // важно!
    width: 100%; // важно!
    margin-bottom: 48px;
  }
}

.review-item {
  // .review-item__header

  &__header {
    display: flex;
    gap: 16px;
    padding-bottom: 16px;
  }

  // .review-item__client-photo

  &__client-photo {
    border-radius: 50%;
  }
  &__client-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }

  // .review-item__client-name

  &__client-name {
    font-weight: 700;
  }

  // .review-item__client-company

  &__client-company {
    font-size: 13px;
    line-height: 147%;
    color: $grey-color;
  }

  // .review-item__bottom

  &__bottom {
    display: flex;
    flex-direction: column;
    gap: 32px;
    padding: 32px;
    background: $light-dark-color;
    border-radius: 10px;
  }

  // .review-item__link

  &__link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    padding: 16px;
    max-width: 100%;
    background: $main-background-color;
    border-radius: 10px;
    transition: all 0.2s;

    &:after {
      content: "";
      flex: 0 0 auto;
      display: block;
      width: 30px;
      height: 30px;
      // background-image: url("../img/reviews/common/review-arrow.svg");
      // background-position: center;
      // background-size: cover;
      // background-repeat: no-repeat;
      background-color: $blue-color;
      -webkit-mask-size: contain;
      -webkit-mask-repeat: no-repeat;
      -webkit-mask-position: center;
      -webkit-mask-image: url("../img/reviews/common/review-arrow.svg");
    }
    &:hover {
      background: $blue-color;

      &::after {
        background-color: $main-font-color;
      }
    }
    &:hover .review-item__link-bot {
      color: $main-font-color;
    }
  }

  // .review-item__link-icon

  &__link-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }

  // .review-item__link-title

  &__link-title {
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    max-height: 30px;
    font-weight: 700;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
  }

  // .review-item__link-bot

  &__link-bot {
    font-size: 13px;
    line-height: 147%;
  }

  // .review-item__reactions

  &__reactions {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__reactions-img {
    width: 25px;
    height: 25px;
    background-size: 25px;
    background-repeat: no-repeat;
  }
  &__reactions-img_fire {
    background-image: url("../img/reviews/common/fire.png");
  }
  &__reactions-img_nice {
    background-image: url("../img/reviews/common/nice.png");
  }

  // .review-item__reactions-button

  &__reactions-button {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px 16px;
    border-radius: 100px;
    font-size: 17px;
    line-height: 140%;
    color: $main-font-color;
    background: $dark-green;
    transition: all 0.2s;
  }
  &__reactions-button:hover {
    background: $green-middle-color;
  }
  &__reactions-button.active {
    background: $main-accent-color;
  }
  &__reactions-button.active:hover {
    background: $green-middle-color;
  }
}
.more {
  position: relative;
  display: none;
  margin: -100px auto 0;
  width: 80%;
  padding: 17px 32px;
  font-size: 22px;
  line-height: 164%;
  border-radius: 10px;
  color: $blue-color;
  border: 1px solid $blue-color;
  background-color: $main-background-color;
  -webkit-box-shadow: 4px -27px 100px 100px rgba(43, 45, 51, 0.44);
  -moz-box-shadow: 4px -27px 100px 100px rgba(43, 45, 51, 0.44);
  box-shadow: 4px -27px 100px 100px rgba(43, 45, 51, 0.44);
}

@media (max-width: 1200px) {
  .page-reviews {
    &__container {
      column-count: 2;
    }

    &__title {
      display: none;
    }
    &__title-mobile {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
    &__item {
      min-width: 280px;
    }
  }
}

@media (max-width: 770px) {
  .more {
    display: block;
  }
  .hidden {
    display: none;
  }
  .page-reviews {
    &__container {
      column-count: 1;
    }
  }
  .review-item {
    &__link {
      justify-content: center;
    }
  }
}

.page-price {
  position: relative;

  // .page-price__wrap
  &__background {
    position: absolute;
    z-index: -1;
    left: 0;
    right: 0;
    top: 210px;
    bottom: 210px;
    background: $light-dark-color;
  }

  // .page-price__container

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
    width: 100%;
    min-height: 676px;
    height: auto;
    padding: 64px 5% 64px 10%;
  }

  // .page-price__info

  &__info {
    width: 350px;
    max-width: 100%;
  }

  // .page-price__title

  &__title {
    margin-bottom: 38px;
  }

  // .page-price__text

  &__text {
    margin-bottom: 48px;
  }

  // .page-price__button

  &__button {
    margin-bottom: 48px;
    padding: 17px 32px;
    flex: 0 0 auto;
  }

  // .page-price__view

  &__view {
    position: relative;
    width: 479px;
    height: 731px;
  }

  .discount {
    position: relative;
    z-index: 1;
    background: $light-dark-color;
    border-radius: 10px;

    &::before {
      content: "";
      position: absolute;
      z-index: -10;
      top: -1px;
      left: -1px;
      right: -1px;
      bottom: -1px;

      background: linear-gradient(
        264.54deg,
        rgba(255, 255, 255, 0.2) 0%,
        #ffffff 50.72%,
        rgba(255, 255, 255, 0.2) 100%
      );
      border-radius: 10px;
    }
    &::after {
      content: "";
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: $dark-color;
      border-radius: 10px;
    }
    &__content {
      position: relative;
      z-index: 1;
      display: flex;
      align-items: center;
      gap: 32px;
      padding: 32px;
      background: linear-gradient(
          264.54deg,
          rgba(255, 255, 255, 0) 0%,
          rgba(255, 255, 255, 0.1) 49.71%,
          rgba(255, 255, 255, 0) 100%
        ),
        rgba(255, 255, 255, 0.05);
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25),
        inset 0px 0px 22px rgba(255, 255, 255, 0.15);
      border-radius: 10px;
    }
    &__info {
      text-align: left;
    }
    &__title {
      font-weight: 700;
    }
    &__img {
      width: 40px;
      height: 40px;
    }
  }

  // .chat__bottom
}
.chat {
  display: flex;
  flex-direction: column;
  align-items: space-between;
  width: 100%;
  height: 100%;
  background-color: $main-font-color;

  // .chat__header

  &__header {
  }

  // .chat__header-top

  &__header-top {
  }

  // .chat__top-icons

  &__top-icons {
    width: 76px;
    height: 36px;
    background-image: url("../img/price/macbtn.png");
    background-repeat: no-repeat;
  }

  // .chat__header-bottom

  &__header-bottom {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 0 16px 16px;
  }

  // .chat__arrow-back

  &__arrow-back {
    width: 30px;
    height: 30px;
    background-image: url("../img/price/arrow_back.svg");
    background-position: center;
    background-repeat: no-repeat;
  }

  // .chat__logo

  &__logo {
    width: 60px;
    height: 60px;
    background-image: url("../img/price/chat_logo.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }

  // .chat__header-text

  &__header-text {
  }

  // .chat__bot-title

  &__bot-title {
    color: $main-background-color;
    font-weight: 700;
    font-size: 21px;
    line-height: 140%;
  }

  // .chat__bot-link

  &__bot-link {
    font-weight: 400;
    font-size: 15px;
    line-height: 22px;
    color: $blue-color;
  }

  // .chat__main

  &__main {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    min-height: 520px;
    padding: 67px 16px 16px;
    background-color: #0a7d23;
    background-image: url("../img/price/window.jpg");
    // background-position: -5px 0 0 0;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__block {
    display: none;
    flex-direction: column;

    animation: slideUp 0.7s ease-out forwards;

    &:not(:last-child) {
      padding-bottom: 8px;
    }
  }
  &__out-block {
    align-items: flex-end;
  }
  &__inc-block {
    align-items: flex-start;
  }
  // .chat__sticker

  &__sticker {
    // width: 200px;
    // height: 200px;
    // background-image: url('../img/price/');
  }

  &__message {
    padding: 8px 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    border-radius: 10px;

    &:not(:last-child) {
      margin-bottom: 8px;
      border-radius: 15px;
    }

    // .chat__message_inc
    &_inc {
      color: $main-background-color;
      background-color: $main-font-color;
      border-radius: 15px 15px 15px 0px;
    }
    // .chat__message_out
    &_out {
      color: $green-middle-color;
      background-color: #dbffec;
      border-radius: 15px 15px 0px 15px;
    }
  }
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px;
  }
  &__bottom-button {
    width: 100%;
    padding: 16px;
    text-align: center;
    cursor: pointer;
    font-weight: 400;
    font-size: 20px;
    background: $light-grey-color;
    border-radius: 10px;
    color: $dark-color;
    transition: background 0.2s;
    &:hover {
      background: #d0d1d2;
    }
  }
}
@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@media (max-width: 1200px) {
  .page-price {
    .container {
      justify-content: space-around;
      padding: 64px 0;
    }

    &__background {
      position: absolute;
