}

// plan

.page-plan {
  // .page-plan__wrap

  // .page-plan__container

  &__container {
    overflow: hidden;
  }
  &__grid {
    display: grid;
    column-gap: 32px;
    row-gap: 190px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    width: 1280px;
    margin-bottom: 60px;
  }

  // .page-plan__title

  &__title-block {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__title_hidden {
    display: block;
  }

  &__title-mobile {
    margin-bottom: 54px;
  }
  &__title-mobile:after {
    left: calc(50% - 220px / 2);
  }

  // .page-plan__block

  &__block {
    position: relative;
    display: flex;
    gap: 32px;
    padding: 52px 0 0 0;
  }

  // .page-plan__preparation

  &__preparation {
    grid-column: 2/4;
  }

  // .page-plan__block-title

  &__block-title {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    font-size: 13px;
    line-height: 147%;
    color: $grey-color;
  }
  &__block-title:after {
    content: "";
    display: block;
    width: 100%;
    height: 6px;
    margin-top: 8px;
    background: $grey-color;
    border-radius: 10px;
  }

  &__block-title_blue {
    color: $blue-color;
  }
  &__block-title_blue:after {
    background: $blue-color;
  }

  //
  ._order3 {
    order: 3;
  }
  ._order2 {
    order: 2;
  }
  //

  &__stage {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 292px;
  }

  &__stage-info-wrap {
    padding: 2px;
  }
  &__stage-info {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 32px;
    border-radius: 9px;
    background-color: $light-dark-color;
    transition: all 0.2s;
  }

  &__stage-info:after,
  &__stage-info:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: -2px;
    right: -2px;
    top: -2px;
    display: block;
    height: 100%;
    opacity: 1;
    border-radius: 10px;
    background: linear-gradient(
      180deg,
      rgba(154, 155, 158, 0.4) 0%,
      rgba(154, 155, 158, 0) 100%
    );
    transition: all 0.4s;
  }

  &__stage-info:after {
    opacity: 0;
    background: linear-gradient(
      180deg,
      #9a9b9e 0%,
      rgba(154, 155, 158, 0) 100%
    );
  }

  &__stage-info:hover:after {
    opacity: 1;
  }

  // payment
  &__stage-info_payment:after,
  &__stage-info_payment:before {
    transition: all 0.4s;
    background: linear-gradient(
      180deg,
      rgba(111, 171, 255, 0.4) 0%,
      rgba(111, 171, 255, 0) 100%
    );
  }

  &__stage-info_payment:hover:after {
    background: linear-gradient(
      180deg,
      #6fabff 0%,
      rgba(111, 171, 255, 0) 100%
    );
  }
  // development
  &__stage-info_development:after,
  &__stage-info_development:before {
    top: auto;
    bottom: -2px;
    background: linear-gradient(
      180deg,
      rgba(113, 184, 114, 0) 0%,
      rgba(113, 184, 114, 0.4) 100%
    );
  }

  &__stage-info_development:hover:after {
    background: linear-gradient(
      180deg,
      rgba(113, 184, 114, 0) 0%,
      #71b872 100%
    );
  }
  // .page-plan__stage-title

  &__stage-title {
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 32px;
    line-height: 138%;
  }

  // .page-plan__stage-text

  &__stage-text {
    margin-bottom: 16px;
  }
  &__stage-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 16px;
    border-radius: 10px;
    background-color: $main-background-color;
    transition: all 0.2s;
    &:hover {
      background-color: $blue-color;
      &::after {
        background-color: $main-font-color;
      }
    }
  }
  &__stage-link:after {
    content: "";
    display: block;
    width: 23px;
    height: 23px;
    // background-image: url("../img/reviews/common/review-arrow.png");
    background-color: $blue-color;
    -webkit-mask-size: contain;
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-position: center;
    -webkit-mask-image: url("../img/reviews/common/review-arrow.svg");
  }

  // .page-plan__stage-custom

  &__stage-custom {
    position: absolute;
    left: 0;
    bottom: -50px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    height: 50px;
    padding-top: 16px;
  }

  // .page-plan__stage-icon

  &__stage-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    font-weight: 700;
  }
  &__stage-icon span {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    text-align: center;
    line-height: 60px;
    background: $light-dark-color;
  }
  &__stage-arrow {
    margin-top: 10px;
  }
  &__stage-arrow_down {
    margin-top: -5px;
  }

  // .page-plan__stage-arrow

  &__stage-arrow_hidden {
    display: none;
  }

  // .page-plan__payment

  // .page-plan__development

  &__development {
    grid-column: 2/5;
    padding: 0;
  }

  &__development &__stage-arrow {
    transform: scale(-1, 1);
  }
  &__development &__stage-info-wrap {
    order: 1;
    height: 100%;
  }
  &__development &__stage-info {
    height: 100%;
  }
  &__development &__stage-custom {
    top: -90px;
    bottom: auto;
    justify-content: flex-end;
    gap: 16px;
  }
  &__development &__stage-icon {
    order: 1;
  }

  &__development &__block-title {
    top: auto;
    bottom: -40px;
    left: 0;
    color: $main-accent-color;
    text-align: right;
  }
  &__development &__block-title:after {
    margin-top: -35px;
    background: $main-accent-color;
  }

  &__block-title_blue {
    color: $blue-color;
  }
  &__block-title_blue:after {
    background: $blue-color;
  }

  // .page-plan__guarantee

  &__guarantee {
    grid-row: 2/3;
    grid-column: 1/2;
    padding: 32px;
    border-radius: 10px;
    border: 6px solid $light-dark-color;
  }
  &__guarantee-container {
  }
}
@media (max-width: 1500px) {
  .page-plan {
    // .page-plan__wrap

    &__wrap {
      padding-left: 0;
      padding-right: 0;
    }

    // .page-plan__container

    &__container {
      overflow: hidden;
      -webkit-overflow-scrolling: touch;
    }
    &__grid {
      display: flex;
      align-items: stretch;
      width: auto;
      margin-bottom: 75px;
      // touch-action: none;
      // -ms-touch-action: none;
      // -webkit-overflow-scrolling: touch;
      // overflow: scroll;
    }
    &__title_hidden {
      display: none;
    }
    &__title-mobile {
      display: block;
    }
    &__development {
      padding: 52px 0 0;
      ._order3 {
        order: 0;
      }
      ._order2 {
        order: 0;
      }
      .page-plan__stage-arrow {
        transform: none;
      }
      .page-plan__block-title {
        bottom: auto;
        top: 0;
        text-align: left;
        &:after {
          margin-top: 8px;
        }
      }
      .page-plan__stage-custom {
        top: auto;
        bottom: -50px;
        flex-direction: row-reverse;
      }
    }
    &__stage-arrow_down {
      display: none;
    }
    &__stage-arrow_hidden {
      display: block;
    }
    &__stage-info-wrap {
      flex: 1 0 auto;
    }
    &__stage-info {
      height: 100%;
    }
    &__stage-info_development:after,
    &__stage-info_development:before {
      top: -2px;
      transform: rotate(180deg);
    }
  }
}

@media (max-width: 770px) {
  .page-plan {
    &__grid {
      display: flex;
    }
    &__title {
      display: none;
    }
    &__title-mobile {
      display: block;
    }
  }
}

.page-answers {
  // .page-answers__title

  &__title {
    margin-bottom: 50px;
  }
}

.answers {
  display: flex;
  flex-direction: column;
  gap: 32px;
  // .answers__item

  &__item {
    padding: 32px;
    overflow: hidden;
    border-radius: 10px;
    cursor: pointer;
    background: $light-dark-color;
  }
  &__item:hover {
    background: $dark-color;
  }

  // .answers__item-info

  &__item-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    width: 100%;
  }

  // .answers__item-title

  &__item-title {
    display: inline-block;
    font-weight: 700;
    font-size: 32px;
    line-height: 138%;
  }

  // .answers__item-text

  &__item-text {
    height: 0;
    overflow: hidden;
    transition: all 0.2s;
    width: 90%;
  }
  &__item-text.active {
    margin-top: 32px;
    opacity: 1;
    transition: all 0.4s;
  }

  // .answers__item-button

  &__item-button {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 70px;
    min-width: 70px;
    border-radius: 10px;
    background: $main-background-color;
    transition: transform 0.3s;
  }
  &__item-button.active {
    transform: rotate(90deg);
    transition: transform 0.3s;
  }
  &__item-button:before {
    content: "";
    display: block;
    width: 5px;
    height: 20px;
    background-color: $light-grey-color;
    border-radius: 10px;
    transform: rotate(-45deg) translate(8px, 0);
  }
  &__item-button:after {
    content: "";
    display: block;
    width: 5px;
    height: 20px;
    border-radius: 10px;
    background-color: $light-grey-color;
    transform: translate(1px, 6px) rotate(45deg);
  }
}

