// page-contacts

.page-contacts {
  // .page-contacts__header

  &__header {
    display: flex;
    justify-content: space-between;
    gap: 32px;
    width: 100%;
    margin-bottom: 32px;
  }

  // .page-contacts__item

  &__item {
    flex: 1 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
    padding: 32px;
    border-radius: 10px;
    background-color: $light-dark-color;
    transition: all 0.2s;
  }

  &__item_left {
    justify-content: left;
  }
  &__item-content {
    flex: 1 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
    &::after {
      content: "";

      display: block;
      width: 23px;
      height: 23px;
      background-image: url("../img/reviews/common/review-arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
  }

  &__item:hover {
    background-color: $dark-color;
  }

  // .page-contacts__item-icon

  &__item-icon {
    flex: 1 0 auto;
    width: 60px;
    height: 60px;
    max-width: 60px;
    max-height: 60px;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
    background-color: $main-background-color;
  }

  &__item-icon_mail {
    background-image: url("../img/contacts/mail.png");
  }
  &__item-icon_robot {
    background-image: url("../img/contacts/robot.png");
  }
  &__item-icon_people {
    background-image: url("../img/contacts/people.png");
  }

  // .page-contacts__item-title

  &__item-title {
    color: $grey-color;
  }
}

.creators {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 32px;
  padding: 32px;
  border-radius: 10px;
  background-color: $light-dark-color;
  transition: all 0.4s;
  // .creators__icon
  &:hover {
    background-color: $dark-color;
  }
}

