.swiper {
  &-slide {
    opacity: 0.4;

    text-align: center;
    flex: 1 0 auto;
  }
  &-slide-active {
    opacity: 1;
  }
  &-back {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgb(43, 45, 51);
    background: linear-gradient(
      270deg,
      rgba(43, 45, 51, 1) 1%,
      rgba(255, 255, 255, 0) 30%,
      rgba(255, 255, 255, 0) 70%,
      rgba(43, 45, 51, 1) 99%
    );
  }
}

.slider {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 32px;

  &__arrow {
    flex: 1 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70px;
    height: 70px;
    background-color: #4294ff;
    background-image: url("../img/cases/arrow.svg");
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    border-radius: 50%;
    transition: all 0.2s;
  }
  &__arrow:hover {
    background-color: #0f70dd;
  }
  &__arrow img {
    width: 30px;
    height: 30px;
  }
  &__arrow-right {
    transform: scale(-1, 1);
  }
}

.header-slider {
  max-width: 796px;
  padding: 32px;
  overflow: hidden; //с ней начал воспринимать ширину!!
  border-radius: 100px;
  background-color: #25272b;
  .swiper {
    &-back {
      background: rgb(37, 39, 43);
      background: linear-gradient(
        270deg,
        rgba(37, 39, 43, 1) 1%,
        rgba(255, 255, 255, 0) 50%,
        rgba(37, 39, 43, 1) 99%
      );
    }
  }
}

@media (max-width: 1200px) {
  .header-slider {
    width: 100%;
    padding: 32px 0;
    background: none;
    .swiper {
      &-back {
        background: rgb(43, 45, 51);
        background: linear-gradient(
          270deg,
          rgba(43, 45, 51, 1) 1%,
          rgba(255, 255, 255, 0) 50%,
          rgba(43, 45, 51, 1) 99%
        );
      }
    }
  }
}

@media (max-width: 770px) {
  .header-slider {
    padding: 0;
    width: 100%;
    // max-width: 380px;
  }
}
@media (max-width: 599px) {
  .header-slider {
    .swiper {
      &-back {
        display: none;
      }
    }
  }
}
