.cases-slider {
  // .cases-slider__arrow
  position: relative;

  &__arrow {
    position: absolute;
    top: 50%;
    left: 16.5%;
    z-index: 100;
  }
  &__arrow-right {
    left: auto;
    right: 16.5%;
  }

  &__slide {
    flex: 1 0 auto;
    display: flex;
    width: 952px;
    transition: all 1s;
  }

  // .cases-slider__slide-view

  &__slide-view {
    position: relative;
    overflow: hidden;
    border-radius: 30px;
    min-width: 316px;
    max-height: 684px;
    margin-top: 64px;
    margin-right: -146px;
  }
  &__slide-view img {
    overflow: hidden;
    border-radius: 30px;
    width: 100%;
    height: 100%;
  }

  // .cases-slider__slide-content

  &__slide-content {
    display: flex;
    background-color: #25272b;
    flex-direction: column;
    gap: 16px;
    padding: 32px 32px 32px 178px;
    border-radius: 20px;
  }

  // .cases-slider__slide-header

  &__slide-header {
    display: flex;
    align-items: center;
    gap: 32px;
  }

  // .cases-slider__slide-icon

  &__slide-icon {
    border-radius: 50%;
  }

  // .cases-slider__slide-title

  &__slide-title {
    font-weight: 700;
    font-size: 32px;
    line-height: 138%;
  }

  // .cases-slider__slide-link

  &__slide-link {
    display: flex;
    align-items: center;

    & > span::after {
      content: "";
      display: inline-block;
      width: 15px;
      height: 15px;
      margin-left: 8px;
      background-image: url("../img/reviews/common/review-arrow.svg");
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  &__slide-link img {
    width: 15px;
    height: 15px;
  }

  // .cases-slider__slide-tags

  &__slide-tags {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  // .cases-slider__slide-tag

  &__slide-tag {
    border: 6px solid #2b2d33;
    border-radius: 10px;
    padding: 16px;
  }

  // .cases-slider__text

  &__text {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 32px;
    border: 6px solid #2b2d33;
    border-radius: 10px;
    text-align: left;
  }

  // .cases-slider__text-title

  &__text-title {
    font-weight: 700;
  }

  // .cases-slider__text-content

  &__text-content {
  }
  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    font-size: 22px;
    line-height: 164%;
    color: #6fabff;
    padding: 17px;
    border: 2px solid #6fabff;
    border-radius: 10px;
    background: none;
    transition: all 0.4s;
  }
  &__button:hover {
    color: #fff;
    padding: 17px;
    border: 2px solid #4294ff;
    background-color: #4294ff;
  }
  &__button > svg > path {
    transition: all 0.4s;
    fill: #6fabff;
  }
  &__button:hover > svg > path {
    fill: #fff;
  }
}
@media (max-width: 1600px) {
  .cases-slider {
    max-width: 960px;
    margin: 0 auto;
    &__arrow {
      left: -9%;
    }
    &__arrow-right {
      left: auto;
      right: -9%;
    }
    .swiper-back {
      display: none;
    }
  }
}
@media (max-width: 1200px) {
  .cases-slider {
    &__arrow {
      display: none;
    }
  }
}
@media (max-width: 770px) {
  .cases-slider {
    &__slide {
      flex-direction: column;
      align-items: center;
      width: 100%;
    }

    // .cases-slider__slide-view

    &__slide-view {
      max-width: 388px;

      max-height: 684px;

      margin: 0 auto 0;
    }
    &__slide-view img {
      overflow: hidden;
      border-radius: 30px;
      width: 100%;
      height: 100%;
    }

    // .cases-slider__slide-content

    &__slide-content {
      background: none;
      padding: 32px;
    }
    &__text {
      border-color: #25272b;
      text-align: left;
    }
    &__slide-header {
      justify-content: center;
    }
    &__title-block {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }

    &__slide-tag {
      border-color: #25272b;
    }
    &__slide-tags {
      justify-content: center;
    }
    &__slide-icon {
      display: none;
    }
  }
}

@media (max-width: 480px) {
  .cases-slider {
    &__slide-content {
      padding: 0;
    }
  }
}
